<template>
    <div id="takeout-order">
        <public-header title="订单设置"></public-header>
        <div class="content">
            <van-cell-group>
                <van-cell>
                    <div slot="title">配送员跑腿订单开启自动刷新</div>
                    <iswitch :value="mall.auto_refresh" condition-open="1" condition-close="2" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="mall.auto_refresh"></iswitch>
                </van-cell>
                <van-cell>
                    <div slot="title">配送员送达后要向顾客获取收货码</div>
                    <iswitch :value="mall.verification_code" condition-open="1" condition-close="2" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="mall.verification_code"></iswitch>
                </van-cell>
            </van-cell-group>
            <van-radio-group v-model="mall.dispatch_mode" class="margin-10-t">
                <van-cell-group>
                    <van-cell title="外卖单派单模式"></van-cell>
                    <van-cell title="抢单模式" clickable @click="mall.dispatch_mode = 1" class="border-0px">
                        <van-radio :name="1" />
                    </van-cell>
                    <van-cell title="管理员派单" clickable @click="mall.dispatch_mode = 2" class="border-0px">
                        <van-radio :name="2" />
                    </van-cell>
                    <van-cell title="系统分配" clickable @click="mall.dispatch_mode = 3" class="border-0px">
                        <van-radio :name="3" />
                    </van-cell>
                </van-cell-group>
            </van-radio-group>
            <van-cell-group class="margin-10-t">
                <van-cell>
                    <div slot="title">管理员派单/系统分配模式允许抢单</div>
                    <iswitch :value="mall.can_collect_order" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="mall.can_collect_order"></iswitch>
                </van-cell>
            </van-cell-group>
            <div class="padding-15">
                <van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
            </div>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				mall: {
					auto_refresh: '1',
					verification_code: '1',
					dispatch_mode: '1',
					can_collect_order: '1'
				}
			}
		},
		components: {
			publicHeader,
			iswitch,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'mall',
					url: 'plateform/errander/config/basic'
				});
			},
			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},
			onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/errander/config/basic',
					data: that.mall
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>

</style>


